@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  font-family: "Poppins";
  text-shadow: #000 0px 0px 2px, #000 0px 0px 2px, #000 0px 0px 2px,
    #000 0px 0px 2px, #000 0px 0px 2px, #000 0px 0px 2px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* :hover {
  text-shadow: none;
}

@media only screen and (min-width: 767px) {
  * {
    cursor: none;
  }
}

.HomeIntroDivFM {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.HomeIntroDiv {
  justify-content: center;
  align-items: center;
  margin: 4%;
}

.HomeIntroDivFM > p {
  color: aliceblue;
  -webkit-font-smoothing: antialiased;
  max-width: 50%;
  margin-top: 2%;
  margin-bottom: 2%;
  text-align: center;
  font-size: 25px;
}

#HomePageDiv > h3 {
  margin: 5%;
  margin-bottom: 3%;
  margin-left: 7%;
  color: aliceblue;

  -webkit-font-smoothing: antialiased;
}

#HomePageDiv > * > * > h1 {
  color: aliceblue;

  -webkit-font-smoothing: antialiased;
  max-width: 64%;
}

#HomePageDiv > * > h2 {
  color: aliceblue;

  -webkit-font-smoothing: antialiased;
  max-width: 64%;
  text-align: left;
  margin-left: 10%;
}

#HomePageDiv {
  min-height: 510px;
}

#HomePageDiv > .HomeAnimations {
  padding-left: 3%;
  padding-right: 3%;
}

/* Lottie Animations */
.Col1 > div,
.Col1 > div > svg,
.Col2 > div,
.Col2 > div > svg,
.Col3 > div,
.Col3 > div > svg {
  max-height: 400px;
  align-items: center;
  vertical-align: middle;
}

.Col1 > h4,
.Col2 > h4,
.Col3 > h4 {
  align-items: center;
  vertical-align: middle;
  color: aliceblue;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  #HomePageDiv > * > * > h1 {
    color: aliceblue;
    max-width: 90%;
    margin-top: 8%;
    margin-bottom: 8%;
  }
  .HomeIntroDivFM > p {
    color: aliceblue;
    max-width: 90%;
    margin-top: 2%;
    margin-bottom: 7%;
    text-align: end;
    font-size: 18px;
  }
}

#header_logo {
  object-fit: contain;
  width: 250px;
}

#header {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  align-self: center;
  vertical-align: middle;
  padding-top: 3%;
  padding-left: 3%;
  padding-right: 3%;
}

#btn_drop_head > * > * > a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  text-decoration: none;
}

#btn_drop_head > * > * > * > * {
  text-shadow: none;
}

#btn_drop_down_head {
  opacity: 85%;
  cursor: pointer;
  background-color: aliceblue;
}

#Header_Buttons {
  width: 450px;
  align-items: center;
  justify-content: space-between;
  float: right;
}
#Header_Buttons > .row {
  float: right;
}

@media only screen and (max-width: 767px) {
  #btnHeadCol1,
  #btnHeadCol2 {
    visibility: hidden;
    display: none;
  }
}

#HeaderMobile {
  background-color: rgba(255, 255, 255, 0.5);
  margin-top: 2%;
  margin-bottom: 2%;
  padding-top: 9px;
  padding-bottom: 0px;
  align-items: center;
  display: flex;
  justify-content: center;
}

#HeaderMobile > h4 {
  font-size: x-small;
  text-shadow: none;
}

@media only screen and (min-width: 767px) {
  #HeaderMobile {
    visibility: hidden;
    display: none;
  }
}

#Footer,
#FooterMobile {
  background: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 5%;
  padding: 3.5%;
  padding-top: 1.5%;
  padding-bottom: 1.5%;
  width: 100%;
  position: absolute;
  overflow: hidden;
}

#Footer > * > *,
#FooterMobile > * > * {
  text-shadow: none;
}

.FooterMobile_forFlex {
  display: flex;
}

#Footer,
#FooterMobile > {
  align-items: center;
  text-shadow: none;
}

@media only screen and (max-width: 767px) {
  #Footer {
    display: none;
    visibility: hidden;
  }
  #Foot_icons > * > * > svg {
    font-size: 2.25rem;
  }

  #Foot_icons > * > a {
    color: inherit;
    text-decoration: none;

    margin-left: 10px;
    margin-right: 10px;
    margin-top: 3px;
    margin-bottom: 3px;
  }

  #FooterMobile > div > a {
    text-decoration: none;
    color: inherit;
  }
  #Foot_icons > div {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

@media only screen and (min-width: 767px) {
  #FooterMobile {
    display: none;
    visibility: hidden;
  }
}

#Foot_icons > * > * > * > svg {
  font-size: 2.25rem;
}

#Foot_icons > * > * > a {
  color: inherit;
  text-decoration: none;
}
#Foot_icons > * > * > a :hover {
  color: white;
  text-decoration: none;
}
#Foot_icons > .row {
  margin-top: 15px;
  margin-bottom: 15px;
}

#Foot_icons > * > * > svg :hover {
  color: white;
  font-size: 3rem;
}

#Footer > div > img {
  width: 100px;
}

#Footer > hr.solid {
  border-left: 0.5px solid #000000;
  border-right: 0.5px solid #000000;
  width: 2px;
  height: 145px;
}

#Footer > div > a {
  color: inherit;
  text-decoration: none;
}

#Footer > div > a:hover {
  color: white !important;
  text-shadow: #000 0px 0px 2px, #000 0px 0px 2px, #000 0px 0px 2px,
    #000 0px 0px 2px, #000 0px 0px 2px, #000 0px 0px 2px !important;
  cursor: pointer;
}

.Footer_Message_Container > .Footer_Message {
  max-width: 360px;
}

